@import "colors";

*{
    padding: 0;
    margin: 0;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
}

.container{
    max-width: 1140px;
    margin: 0 auto;
}

header{
    .header{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 30px;
        align-items: center;
        .humburger_menu{
            display: none;
            span{
                display: block;
                width: 28px;
                height: 2px;
                margin-bottom: 6px;
                margin-right: 20px;
                background-color: $black;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
        .header_logo{
            flex-basis: 60%;
        }
        .header_tel{
            flex-basis: 20%;
            text-align: end;
            margin-right: 20px;
            h3{
                font-size: 16px;
            }
            small{
                color: $grey;
            }
        }
        .header_btn{
            flex-basis: 15%;
            text-align: end;
            .btn_contacts{
                align-items: flex-end;
                padding: 10px 20px 10px 18px;
                border: 2px solid $black;
                cursor: pointer;
                background-color: $white;
                transition: all 0.3s;
                &:hover{
                    background-color: $yellow;
                }
            }
        }
    }
    nav{
		list-style: none;
        margin-top: 25px;
        background-color: $yellow;
        justify-content: space-around;
        ul{
            display: flex;
            flex-wrap: wrap;
            // justify-content: space-around;
            list-style: none;
            li{
                transition: all 0.2s;
                // max-width: 16.66%;
                text-align: center;
                &:hover{
                    background-color: $grey;
                }
                a{
                    // width: 100%;
                    padding: 24px;
                    display: block;
                    text-decoration: none;
                    color: $black;
                    font-size: 16px;
                }
            }
        }
        // li{ 
        //     display: flex;
        //     flex-wrap: wrap;
        //     // z-index: 9999;
        //     // display: block;
            
        // }
    }
}

