.wrapper{
    h2{
        text-transform: uppercase;
        font-size: 20px;
        text-align: center;
        margin-top: 100px;
        margin-bottom: 60px;
    }
    .presence{
        .box{
            max-width: 1140px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            &_item{
                border: 1px solid #e8e8e8;
                flex: 0 0 250px;
                text-align: center;
                margin: 0 20px 10px 20px;
                padding: 20px;
                img{
                    width: 260px;
                    height: 140px;
                }
                h4{
                    font-size: 16px;
                    margin-top: 45px;
                    margin-bottom: 20px;
                }
                p{
                    color: $grey;
                    font-size: 13px;
                }
                h3{
                    margin-top: 30px;
                    margin-bottom: 30px;
                    font-size: 20px;
                    font-weight: 500;
                }
                button{
                    background-color: $yellow;
                    padding: 20px 56px 20px 56px;
                    cursor: pointer;
                    &:hover{
                        background-color: rgb(255, 234, 0);
                    }
                }
            }
        }
    }
    .advantage{
        background-color: hsl(0, 0%, 96%);
        margin-top: 100px;
        padding-bottom: 100px;
        h2{
            padding-top: 100px;
        }
        .box{
            max-width: 1140px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            &_item{
                border: 1px solid #e8e8e8;
                flex: 0 0 270px;
                background-color: $white;
                text-align: center;
                padding: 40px;
                margin: 10px;
                img{
                    min-width: 47px;
                    min-height: 50px;
                }
                p{
                    color: $grey;
                    font-size: 13px;
                }
                h3{
                    margin-top: 30px;
                    margin-bottom: 30px;
                    font-size: 16px;
                    font-weight: 500;
                }
            }
        }
    }
    .popular_goods{
        margin-top: 100px;
        .box{
            max-width: 1140px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            &_item{
                border: 1px solid #e8e8e8;
                flex: 0 0 220px;
                background-color: $white;
                text-align: center;
                padding: 20px;
                margin: 10px;
                &_img-goods{
                    position: relative;
                    .sale{
                        position: absolute;
                        top: 10px;
                        left: 10px;
                        background-color: $red;
                        width: 60px;
                        padding: 4px 0 4px 0;
                        color: $white;
                        text-align: center;
                    }
                }
                p{
                    margin: 8px 0 8px 0;
                }
                &_price{
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    justify-content: center;
                    small{
                        color: $grey;
                        text-decoration: line-through;
                    }
                    p{
                        font-size: 18px;
                        margin-left: 10px;
                    }
                    p[data-sale]{
                        color: $red;
                    }
                }
                button{
                    background-color: $yellow;
                    padding: 16px 50px 16px 50px;
                    margin-top: 20px;
                    cursor: pointer;
                    &:hover{
                        background-color: rgb(255, 234, 0);
                    }
                }
                
            }
        }
    }
    .service{
        background-color: hsl(0, 0%, 96%);
        margin-top: 100px;
        padding-bottom: 100px;
        h2{
            padding-top: 100px;
        }
        .box{
            max-width: 1140px;
            margin: 0 auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            &_item{
                border: 1px solid #e8e8e8;
                flex: 0 0 300px;
                background-color: $white;
                text-align: center;
                padding: 38px 24px 38px 24px;
                margin: 10px;
                img{
                    min-height: 121px;
                    max-width: 250px;
                }
                a{
                    text-decoration: none;
                    color: $black;
                    h3{
                        margin-top: 20px;
                        font-size: 16px;
                    }
                    &:hover{
                        color: $yellow;
                    }
                }
            }
        }
    }
    .contacts{
        background: url(/build/img/contacts/background_map.png) center center  no-repeat;
        height: 520px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        &_item{
            &:last-child{
                margin-left: 110px;
            }
        }
        &_info{
            background-color: $white;
            padding: 60px;
            h3{
                padding-bottom: 50px;
                font-size: 20px;
                font-weight: 700;
                text-transform: uppercase;
            }
            .tel{
                padding-bottom: 10px;
                position: relative;
                p{
                    margin-bottom: 10px;
                    font-size: 16px;
                }
                &::before{
                    content: '';
                    position: absolute;
                    left: -30px;
                    top: 16px;
                    display: block;
                    background: url(/build/img/contacts/icon-phone.png);
                    height: 12px;
                    width: 12px;
                }
            }
            .map{
                position: relative;
                padding-bottom: 20px;
                font-size: 16px;
                &::before{
                    content: '';
                    position: absolute;
                    left: -30px;
                    top: 10px;
                    background: url(/build/img/contacts/icon-map.png) no-repeat;
                    padding: 10px;
                    height: 12px;
                    width: 12px;
                }
            }
            .email{
                position: relative;
                font-size: 16px;
                color: $yellow;
                &::before{
                    content: '';
                    position: absolute;
                    left: -30px;
                    top: 4px;
                    background: url(/build/img/contacts/icon-mail.png) no-repeat;
                    padding: 10px;
                    height: 12px;
                    width: 12px;
                }
            }
        }
    }
}
footer{
    background-color: $footer;
    padding: 40px;
    .footer{
        a{
            display: block;
            margin-bottom: 10px;
            cursor: pointer;
        }
        small{
            color: $white;
        }
        &_contacts{
            text-align: right;
            line-height: 18px; // update
            margin-top: 6px;
            h3{
                font-size: 18px;
                color: $white;
            }
            p{
                font-size: 12px;
            color: $grey;
            }
            &_btn{
                align-items: flex-end;
                margin-top: 10px;
                padding: 10px 20px 10px 18px;
                border: 2px solid $white;
                cursor: pointer;
                color: $white;
                background-color: $footer;
                transition: all 0.3s;
                &:hover{
                    background-color: $yellow;
                    color: $black;
                }
            }
        }
    }
}